<template>
    <header class="header">
        <nav class="header__nav">
            <img :src="avatar[src]" alt="" class="header__nav-img">
            <div class="header__nav-info">
                <h2 class="header__nav-info-name">{{ name }}</h2>
                <p class="header__nav-info-status">{{ status }}</p>
            </div>
        </nav>
    </header>
</template>

<script>
    export default {
        props: ['src', 'name', 'status'],
        data() {
            return {
                avatar: {
                    user1: require('@/assets/images/user1.png'),
                    user2: require('@/assets/images/user2.png'),
                }
            }
        },
    }
</script>