<template>
  <div class="wrapper">
    <div class="wrapper__window">
      <Header
        :src="'user2'"
        :name="'Александр'"
        :status="'Онлайн'" 
      />
      <Main
        :windowId="2"
        :messages="messages"
      />
      <Footer
        :windowId="2"
        @sendMessage="sendMessage"
      />
    </div>
    <div class="wrapper__window">
      <Header 
        :src="'user1'"
        :name="'Евгений'"
        :status="'Онлайн'" 
      />
      <Main 
        :windowId="1"
        :messages="messages"
      />
      <Footer 
        :windowId="1"
        @sendMessage="sendMessage"
      />
    </div>
  </div>
</template>

<script>
import Header from './components/Header.vue';
import Main from './components/Main.vue';
import Footer from './components/Footer.vue';

export default {
  components: {
    Header,
    Main,
    Footer,
  },
  data() {
    return {
      messages: [],
    }
  },
  created() {
    const message = localStorage.messages ? JSON.parse(localStorage.messages) : [];
    this.messages = message
  },
  methods: {
    sendMessage(messageText) {
      this.messages.push(messageText)
      localStorage.messages = JSON.stringify(this.messages)
    }
  },
}
</script>