<template>
    <main class="main">
        <div v-for="(message, id) in messages" :key="id" class="main__chat">
            <div class="main__chat-message" 
                :class="{
                    messagesend: message.windowId == windowId, 
                    messageget: message.windowId != windowId,
                    messageimage: message.image?.length > 0 && message.messageText.length == 0,
                    'image-text': message.image?.length > 0 && message.messageText.length > 0
                }">
                <span class="main__chat-message-time">{{ message.time }}</span>
                <div class="main__chat-message-text">
                    <img  v-if="message.image?.length > 0" :src="message.image" alt="">
                    <p>{{ message.messageText }}</p>
                </div>
            </div>
        </div>
    </main>
</template>

<script>

    export default {
        props: {
            messages: {
                typeof: Array
            },
            windowId: {
                typeof: Number
            },
        },
    }
</script>