<template>
    <footer class="footer">
        <textarea v-model="text" rows="1" placeholder="Написать сообщение..." class="footer__text"></textarea>
        <button v-if="text.length == 0" @click="isModalOpen = true" class="footer__button">
            <img src="@/assets/images/camera.svg" alt="">
        </button>
        <button v-else @click="sendMessage" class="footer__button">
            <img src="@/assets/images/send.svg" alt="">
        </button>
    </footer>
    <div v-if="isModalOpen" @click="isModalOpen = false" class="modal">
        <div @click.stop class="modal__block">
            <h2 class="modal__block-title">Отправить картинку</h2>
            <div class="modal__block-fields">
                <label>
                    <span>URL</span>
                    <input v-model="image">
                </label>
                <label>
                    <span>Комментарий</span>
                    <textarea v-model="comment"></textarea>
                </label>
            </div>
            <div class="modal__block-btns">
                <button @click="isModalOpen = false" class="modal__block-btn cancel">Отмена</button>
                <button @click="sendImage" class="modal__block-btn send">ОТПРАВИТЬ</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        emits: ['sendMessage'],
        data() {
            return {
                text: '',
                image: '',
                comment: '',
                isModalOpen: false
            }
        },
        props: {
            windowId: {
                typeof: Number
            }
        },
        methods: {
            sendMessage() {
                const time = `${new Date().getHours()}:${new Date().getMinutes()}`
                if(this.text.length > 0) {
                    const messageText = {};
                    messageText.messageText = this.text.trim()
                    messageText.windowId = this.windowId
                    messageText.time = time
                    this.$emit('sendMessage', messageText)
                    this.text = ''
                }
            },
            sendImage() {
                const time = `${new Date().getHours()}:${new Date().getMinutes()}`
                if(this.image.length > 0) {
                    const messageText = {};
                    messageText.image = this.image.trim()
                    messageText.messageText = this.comment.trim()
                    messageText.windowId = this.windowId
                    messageText.time = time
                    this.$emit('sendMessage', messageText)
                    this.comment = this.image = ''
                    this.isModalOpen = false
                }
            }
        },
    }
</script>